import Vue from 'vue';
import Router from 'vue-router';
// import Hello from '@/components/Hello';
import Projects from '@/components/Projects';
import Home from '@/components/Home';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/projects',
      name: 'Projects',
      component: Projects,
    },
  ],
});



